@import url(https://fonts.googleapis.com/css2?family=Luckiest+Guy&family=Open+Sans&display=swap);
/* Variables */

:root {
  --font-text: 'Open Sans', sans-serif;
  --font-heading: 'Luckiest Guy', cursive;
  --color-dk-cyan: hsl(191, 85%, 31%);
  --color-deep-sky-aqua: hsl(191, 100%, 40%);
  --color-md-steel-blue: hsl(211, 47%, 29%);
  --color-deep-navy-blue: hsl(220, 60%, 20%);
  --color-aqua: hsl(176.82, 93.41%, 64.31%);
  --color-matte-gold: hsl(36, 36%, 62%);
  --color-very-lt-gray: hsl(0, 0%, 93%);

}

* {
  /*outline: 1px solid black;*/
}

body {
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
  background-color: hsl(36, 36%, 62%);
  background-color: var(--color-matte-gold);
;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
}

.container {
  width: 100vh;
  height: 100%;
  

}

.drum {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 600px;
  padding: 15px;
  background-color: var(--color-deep-navy-blue); 
  margin: 48px auto;
  /*border-radius: 8px;*/
  outline: 5px solid var(--color-deep-navy-blue);

}

.drum-display {
  width: 240px;
  background-color: var(--color-deep-navy-blue);
  height: 5px;
  color: var(--color-matte-gold);
  text-transform: uppercase;
  font-size: 26px;
  font-weight: 700;
  padding: 30px 5px;
  text-align: center;
  margin: 30px 0;
  border: 1px solid var(--color-matte-gold);
}


.pad-bank {
 display: inline-block;
 margin: 10px;
 padding-top: 20px;
 padding-bottom: 20px;
 width: 100px * 3 + 32;
 height: 80px * 3 + 32;
}

.control-container {
  margin-top: -30px;
  margin-bottom: 40px;
}

.control {
  width: 100px;
  margin: auto;
}
p   {
  font-family: var(--font-text);
  font-size: 24px;
  color: var(--color-very-lt-gray);
  margin-bottom: 0;
  text-align: center !important;
}

.select {
  margin: auto;
  border: 1px solid black;
  width: 50px;
  height: 20px;
  padding: 1px;
  background-color: black;
}

.inner {
  width: 23px;
  height: 19px;
  background: var(--color-matte-gold);
  border: 1px solid black;
  box-sizing: border-box;
  cursor: pointer;
}

.volume-slider {
  background: black;
  margin: 10px 0;

}

input {
  width: 250px;
  margin: 5px 0;
}

@media (min-width: 576px) {
  .drum {
    max-width: 385px;
  }
  .drum-machine p {
    font-size: 20px;
  }
  .drum-display {
    font-size: 28px;
  }
}

.jumbotron {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    margin-bottom: 20px;
    text-align: center;
}

h1 {
    font-family: var(--font-heading);
    font-size: 46px;
    padding: 20px;
}

@media screen and (max-width: 576px) {
    .jumbotron {
        text-align: center;
    }
    .jumbotron .header-title  {
        font-size: 34px;
    }

}
footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100px;
}

footer p {
    font-family: var(--font-text);
    color: var(--color-deep-navy-blue);
    
}

.footer-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
       
}

@media screen and (max-width: 576px){
    p {
        font-size: 12px;
    }
}
.drum-pad {
    display: flex;
    justify-content: center;
    position: relative;
    float: right;
    width: 100px;
    height: 80px;
    margin-right: 20px;
    border-radius: 5px;
    padding-top: 35px;
    box-sizing: border-box;
    cursor: pointer;
}
